@font-face {
    font-family: 'montserrat_mediumregular';
    src: url('../fonts/montserrat-medium-webfont.eot');
    src: url('../fonts/montserrat-medium-webfont.eot?#iefix') format('embedded-opentype'),
         url('../fonts/montserrat-medium-webfont.woff2') format('woff2'),
         url('../fonts/montserrat-medium-webfont.woff') format('woff'),
         url('../fonts/montserrat-medium-webfont.ttf') format('truetype'),
         url('../fonts/montserrat-medium-webfont.svg#montserrat_mediumregular') format('svg');
    font-weight: normal;
    font-style: normal;
}
*{  margin: 0;
    padding: 0;
    box-sizing: border-box;
}
@mixin text-head {
    font: {
        family: 'montserrat-medium-webfont' ,Tahoma , Arial, sans-serif;
        size: 32px;
        weight: 500;
    }
    line-height: 1;
}
@mixin text-description{
    font: {
        size: 18px;
    }
    color: #787878;
}
a{
    text-decoration: none;
    color: #000;
}
input{
    outline: none;
}
ul{
    list-style: none;
}
img{
    max-width: 100%;
    height: auto;
}
.wrapper{
    overflow: hidden;
}
.container{
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
}

body{
    font: 16px 'montserrat-medium-webfont' ,Tahoma , Arial, sans-serif;
    line-height: 24px;
}
#main-banner{
    min-height: 100vh;
}
.banner-top{
    padding: 38px 0 175px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.banner-icon{
    flex-grow: 1;
    width: 30px;
    height: 26px;
    position: relative;
}
.banner-icon::after{
    content: "";
    display: block;
    width: 30px;
    height: 26px;
    position: absolute;
    top: 0;
    left: 0;
    background: url('../images/logo.png') no-repeat 50%;
}
.navigation > ul{
    display: flex;
    text-transform: uppercase;
}
.navigation{
    position: relative;
}
.inner-nav li{
    padding-right: 30px;
}
.inner-nav li:first-child a{
    color: #10c9c3;
}
.inner-nav li:last-child{
    padding: 0;
}
.banner-bottom h2{
    @include text-head;
    padding-bottom: 28px;
}
.banner-bottom{
    display: flex;
    flex-direction: row-reverse;
}
.banner-bottom>div{
    max-width: 500px;
}
.banner-bottom p{
    padding-bottom: 36px;
    @include text-description;
}
.banner-bottom a{
    display: block;
    color: #fff;
    line-height: 1;
    text-transform: uppercase;
    padding: 22px 0;
    background: #10c9c3;
    text-align: center;
}
#about h2{
    padding: 100px 0 34px;
    @include text-head;
    text-align: center; 
}
#about p{
    @include text-description;
    max-width: 626px;
    text-align: center;
    padding-bottom: 34px;
}
.banner-bottom p+div{
    width: 50%;
}
#about h2 + div{
    display: flex;
    justify-content: center;
    align-items: center;
}
.signature{
    position: relative;
    height: 58px;
    margin-bottom: 100px;
}
.signature::after{
    content: "";
    display: block;
    width: 222px;
    height: 60px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    background: url("../images/signature.png") no-repeat 50%;
}
.mobile-menu {
    position: absolute;
    width: 40px;
    height: 3px;
    background: #000;
    cursor: pointer;
    margin-right: 30px;
    display: none;
    outline: none;
    top:0;
    right: 0;
}
.mobile-menu:after {
    content: "";
    position: absolute;
    right: 0;
    top: 100%;
    padding-top: 5px;
    border-bottom: 3px solid #000;
    width: 40px;
    height: 3px;
}
.mobile-menu:before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 100%;
    padding-bottom: 5px;
    border-top: 3px solid #000;
    width: 40px;
    height: 3px;
}
@media(max-width:768px){
    .mobile-menu{
        display: block;
    }
    .inner-nav{
        display: block;
        position: absolute;
        top: 100%;
        width: 300px;
        background: #000;
        -webkit-transition: 0.3s;
        -o-transition: 0.3s;
        transition: 0.3s;
        right: -500px;
        max-height: calc(100vh - 52px);
        overflow-y: auto;
        height: 100vh;
    }
    .banner-icon{
        margin-left: 30px;
    }
    .banner-top{
        padding: 30px 0 80px;
    }
    .banner-bottom>div{
        max-width: none;
    }
    .banner-bottom h2{
        text-align: center;
    }
    .banner-bottom p+div{
        width: 100%;
        display: flex;
        justify-content: center;
    }
    .banner-bottom p{
        text-align: center;
    }
    .banner-bottom a{
        width: 50%;
    }
    #about h2{
        padding: 50px 0 28px;
    }
    .signature{
        margin: 0;
    }
}